<!--  -->
<template>
    <div class="realTime-wrapper" :style="`height:${boxHeight};width:${boxWidth}`">
        <div class="realTime-head">
            <!-- 价格数量时间 -->
            <div class="price cell">{{$t('contract.realTime[0]')}}(USDT)</div>
            <div class="number cell">{{$t('contract.realTime[1]')}}</div>
            <div class="time cell">{{$t('contract.realTime[2]')}}</div>
        </div>
        <ul class="realTime-list">
            <!-- 暂无数据 -->
            <div class="noData">{{$t('contract.realTime[3]')}}</div>
        </ul>
    </div>
</template>

<script>
    export default {
        props: {
            height: String,
            width: String
        },
        data() {
            return {
            };
        },
        computed: {
            boxHeight() {
                if (this.height) return this.height
                else return ''
            },
            boxWidth() {
                if (this.width) return this.width
                else return ''
            },
        },

        methods: {
            // 推送更新
            update(bus) {
                var dom = document.querySelector('.realTime-list')
                var html = ""
                var data = bus.data.data
                for (var i = 0; i < data.length; i++) {
                    html += `
                     <li>
                        <span class="price cell"><span class="${data[i].dc=='buy'? 'green':'red'}">${data[i].price}</span></span>
                        <span class="number cell">${data[i].amount}</span>
                        <span class="time cell">${this.$public.getLocalTime(data[i].dt) }</span>
                    </li>`
                }
                dom.innerHTML = html
            },
            // 切换币种时清空
            clear() {
                var dom = document.querySelector('.realTime-list')
                dom.innerHTML = ""
            }
        },
        created() {
            this.$bus.$on('Mark_change', this.clear)
            this.$bus.$on('realTime_updated', this.update)
        },
        beforeDestroy() {
            this.$bus.$off('Mark_change', this.clear)
            this.$bus.$off('realTime_updated', this.update)
        }


    }

</script>
<style lang=less>
    @import '../../../assets/index.less';
    @import './realTime.less';
</style>


