<!--  -->
<template>
    <div class="trader-wrapper">
        <!-- 市价/限价选择 -->
        <div class="trader-head">
            <div class="btn-wrap">
                <div class="btn" @click="changeType(true)" :class="isMarket? 'active' : ''">{{$t('contract.trader[0]')}}</div>
                <div class="btn" @click="changeType(false)" :class="isMarket? '' : 'active'">{{$t('contract.trader[1]')}}</div>
            </div>
        </div>
        <div class="trader-main">
            <!-- 可用 -->
            <div class="usable">
                <span>{{$t('contract.trader[2]')}}</span>
                <span>{{usabel}} USDT</span>
            </div>
            <div class="trader-form-wrap">
                <!-- 空label占位用 误删 方便后期加字 -->
                <!-- 买！！！！ form -->
                <div class="trader-form-buy trader-form">
                    <!-- 价格 -->
                    <div class="trader-form-item item-price">
                        <label for="">{{$t('contract.trader[3]')}}</label>
                        <el-input :disabled="isMarket" v-model="buy.newprice" :placeholder="$t('contract.trader[4]')">
                            <div v-show="!isMarket" class="ipt-suffix ipt-suffix-change" slot="suffix">
                                <i class="el-icon-minus" @click="countNum('buy', '-')"></i>
                                <i class="el-icon-plus" @click="countNum('buy', '+')"></i>
                            </div>
                        </el-input>
                    </div>
                    <!-- 数量 -->
                    <div class="trader-form-item">
                        <label for="">{{$t('contract.trader[5]')}}</label>
                        <el-input v-model.number="buy.buynum" :placeholder="`${pcsPrice+markData.name}/${$t('contract.trader[6]')}`">
                            <div class="ipt-suffix" slot="suffix">{{$t('contract.trader[6]')}}</div>
                        </el-input>
                    </div>
                    <!-- 比例 -->
                    <div class="trader-form-item item-rate">
                        <label for=""></label>
                        <div class="rate">
                            <div @click="changeOutRate(0.25, 'buyOutRate')" :class="buyOutRate =='0.25'? 'active' : ''">
                                25%</div>
                            <div @click="changeOutRate(0.5, 'buyOutRate')" :class="buyOutRate =='0.5'? 'active' : ''">
                                50%</div>
                            <div @click="changeOutRate(0.75, 'buyOutRate')" :class="buyOutRate =='0.75'? 'active' : ''">
                                75%</div>
                            <div @click="changeOutRate(1, 'buyOutRate')" :class="buyOutRate =='1'? 'active' : ''">100%
                            </div>
                        </div>
                    </div>
                    <!-- 杠杆 -->
                    <div class="trader-form-item">
                        <label for="">{{$t('contract.trader[7]')}}</label>
                        <el-select v-model="buy.leverage" :placeholder="$t('contract.trader[8]')">
                            <el-option v-for="(item,index) in leverageArr" :label="item" :value="item" :key="index">
                            </el-option>
                        </el-select>
                    </div>
                    <!-- 占用保证金 -->
                    <div class="bond"><span>{{$t('contract.trader[9]')}}:</span><span>{{buyTotal}} USDT</span></div>
                    <div class="btn-submit buy" @click="createOrder(buy)">
                        <!-- 买入/做多 -->
                        {{$t('contract.trader[10]')}}
                    </div>
                </div>
                <!-- 卖！！！！！ form -->
                <div class="trader-form-sell trader-form">
                    <!-- 价格 -->
                    <div class="trader-form-item item-price">
                        <label for=""></label>
                        <el-input :disabled="isMarket" v-model="sell.newprice" :placeholder="$t('contract.trader[11]')">
                            <div v-show="!isMarket" class="ipt-suffix ipt-suffix-change" slot="suffix">
                                <i class="el-icon-minus" @click="countNum('sell', '-')"></i>
                                <i class="el-icon-plus" @click="countNum('sell', '+')"></i>
                            </div>
                        </el-input>
                    </div>
                    <!-- 数量 -->
                    <div class="trader-form-item">
                        <label for=""></label>
                        <el-input v-model.number="sell.buynum" :placeholder="`${pcsPrice+markData.name}/${$t('contract.trader[6]')}`">
                            <div class="ipt-suffix" slot="suffix">{{$t('contract.trader[6]')}}</div>
                        </el-input>
                    </div>
                    <!-- 比例 -->
                    <div class="trader-form-item item-rate">
                        <label for=""></label>
                        <div class="rate">
                            <div @click="changeOutRate(0.25, 'sellOutRate')"
                                :class="sellOutRate =='0.25'? 'active' : ''">25%</div>
                            <div @click="changeOutRate(0.5, 'sellOutRate')" :class="sellOutRate =='0.5'? 'active' : ''">
                                50%</div>
                            <div @click="changeOutRate(0.75, 'sellOutRate')"
                                :class="sellOutRate =='0.75'? 'active' : ''">75%</div>
                            <div @click="changeOutRate(1, 'sellOutRate')" :class="sellOutRate =='1'? 'active' : ''">100%
                            </div>
                        </div>
                    </div>
                    <!-- 杠杆 -->
                    <div class="trader-form-item">
                        <label for=""></label>
                        <el-select v-model="sell.leverage" :placeholder="$t('contract.trader[8]')">
                            <el-option v-for="(item,index) in leverageArr" :label="item" :value="item" :key="index">
                            </el-option>
                        </el-select>
                    </div>
                    <!-- 占用保证金 -->
                    <div class="bond"><span>{{$t('contract.trader[9]')}}:</span><span>{{sellTotal}} USDT</span></div>
                    <div class="btn-submit sell" @click="createOrder(sell)">
                        {{$t('contract.trader[12]')}}
                    </div>
                </div>
            </div>
            <!-- 下单对话框 -->
            <el-dialog custom-class='reset-dialog' :visible.sync="dialog" width="400px" :before-close="dialog_close">
                    <!-- 确认订单  -->
                <h1 slot="title">{{$t('contract.trader[13]')}}</h1>
                <div class="set-line">
                    <div class='item'>
                            <!-- 方向 -->
                        <label for="">{{$t('contract.trader[14]')}}</label>
                        <!-- otype 1 买入 2 卖出 -->
                        <div :class="dialogData.otype=='1'?'green':'red'">{{dialogData.otype=='1'? $t('contract.trader[22]'):$t('contract.trader[23]')}}</div>
                    </div>
                    <div class='item'>
                            <!-- 类型 -->
                        <label for="">{{$t('contract.trader[15]')}}</label>
                        <!-- type 1 市价 2 限价 -->
                        <div>{{dialogData.type =='1'?$t('contract.trader[16]'):$t('contract.trader[17]')}}</div>
                    </div>
                </div>
                <div class="set-line">
                    <div class='item'>
                            <!-- 价格/ -->
                        <label for="">{{$t('contract.trader[3]')}}</label>
                        <div>{{orderPrice}}</div>
                    </div>
                    <div class='item'>
                            <!-- 数量 -->
                        <label for="">{{$t('contract.trader[5]')}}</label>
                        <div>{{dialogData.buynum}}</div>
                    </div>
                </div>
                <div class="set-line">
                    <div class='item'>
                            <!-- 保证金 -->
                        <label for="">{{$t('contract.trader[24]')}}</label>
                        <div>{{dialogData.total}}</div>
                    </div>
                     <!-- 手续费 -->
                    <!-- <div class='item'>
                        <label for="">{{$t('contract.trader[18]')}}</label>
                        <div>{{fee}}</div>
                    </div> -->
                    <div class='item'>
                            <!-- 杠杆倍数 -->
                        <label for="">{{$t('contract.trader[19]')}}</label>
                        <div>{{dialogData.leverage}}</div>
                    </div>
                </div>
                <span slot="footer" class="dialog-footer">
                        <!-- 取 -->
                    <el-button class="btn-close btn" @click="dialog_close">{{$t('contract.trader[20]')}}</el-button>
                    <!-- 确 -->
                    <el-button class="btn-confirm btn" :loading="loading" type="primary" @click="submitOrder">{{$t('contract.trader[21]')}}
                    </el-button>
                </span>
            </el-dialog>
        </div>
    </div>
</template>

<script>
    import { get_lever, create_order } from '@/api/contract'
    export default {
        data() {
            return {
                // mobile	是	int	手机号码
                // newprice	是	int	委托价格
                // type	是	int	市价 1 限价 2
                // otype	是	int	涨 1 跌 2
                // buynum	是	int	购买数量
                // shopname	是	string	币种标识 btc_usdt
                // zy	是	int	止盈
                // zs	是	int	止损
                // leverage	是	int	杠杆
                loading: false,
                dialog: false,
                dialogData: "",
                markData: "",
                // 是否市价
                isMarket: true,
                // 可用余额
                usabel: "",
                // 当前杠杆倍数数组
                leverageArr: [],
                // 张单价
                pcs_price: "",
                // 点差
                spread: "",
                // 最小变动价
                var_price: "",
                // 手续费比例
                trans_fee: "",
                sell: {
                    // 当前所选杠杆
                    leverage: "",
                    buynum: "",
                    newprice: "",
                    otype: 2,
                    shopname: "",
                    type: "1"
                },
                buy: {
                    // 当前所选杠杆
                    leverage: "",
                    buynum: "",
                    newprice: "",
                    otype: 1,
                    shopname: "",
                    type: "1"
                },

                // 出资比例
                buyOutRate: "",
                sellOutRate: "",
                input: '',
                value: ""
            };
        },
        watch: {
            'isMarket'(n) {

                if (n) {
                    this.buy.newprice = this.markData.price
                    this.sell.newprice = this.markData.price
                    this.buy.type = "1"
                    this.sell.type = "1"
                } else {
                    this.buy.type = "2"
                    this.sell.type = "2"
                }
                this.clearForm()
            },
            'buy.newprice'(n, o) {
                var de = String(n).split(".")
                // 非数字限制
                if (isNaN(Number(n))) {
                    this.buy.newprice = o
                }
                // 位数限制 
                if (de.length == 2) {
                    if (de[1].length > this.savePoint) {
                        this.buy.newprice = o
                        console.log('buy.newprice 超出位数限制');
                    }
                }
                if (!this.isMarket) {
                    this.buyOutRate = ""
                }
            },
            'sell.newprice'(n, o) {
                var de = String(n).split(".")
                // 非数字限制
                if (isNaN(Number(n))) {
                    this.sell.newprice = o
                }
                // 位数限制 位数限制 限价时
                if (de.length == 2) {
                    if (de[1].length > this.savePoint) {
                        this.sell.newprice = o
                        console.log('sell.newprice 超出位数限制');
                    }

                }
                if (!this.isMarket) {
                    this.sellOutRate = ""
                }
            },
            'buy.buynum'(n, o) {
                if (!sessionStorage.token) {
                    var _this = this
                    this.$public.throttle(() => { this.$public.msg('请登录！', 'warning', this) }, 1500,'t1')
                    this.buy.buynum = ""
                    return
                }
                if(this.maxBuyNum <1){
                    this.$public.throttle(() => { this.$public.msg('余额不足，请充值！', 'warning', this) }, 1500,'t2')
                }
                // 超出最大购买量
                if (n > this.maxBuyNum) {
                    this.buy.buynum = this.maxBuyNum
                }
            },
            'sell.buynum'(n, o) {
                if (!sessionStorage.token) {
                    this.$public.throttle(() => { this.$public.msg('请登录！', 'warning', this) }, 1500, 't3')
                    this.sell.buynum = ""
                    return
                }
                if(this.maxSellNum <1){
                    this.$public.throttle(() => { this.$public.msg('余额不足，请充值！', 'warning', this) }, 1500,'t2')
                }
                // 超出最大购买量
                if (n > this.maxSellNum) {
                    this.sell.buynum = this.maxSellNum
                }
            },
            'buy.leverage'(n, o) {
                if (this.buyOutRate) {
                    this.buy.buynum = this.maxBuyNum
                }
            },
            'sell.leverage'(n, o) {
                if (this.buyOutRate) {
                    this.sell.buynum = this.maxSellNum
                }
            },
            'buyOutRate'(n, o) {
                if (n == "") return
                this.buy.buynum = this.maxBuyNum
            },
            'sellOutRate'(n, o) {
                if (n == "") return
                this.sell.buynum = this.maxSellNum
            }
        },
        components: {},
        computed: {
            // 实际下单价  市价的时候 实际下单价为 buy : 最新价 + 点差*最小变动价 sell ： 最新价 - 点差*最小变动价
            // 下单对话框显示和发送数据用
            orderPrice() {
                if (this.isMarket) {
                    if (this.dialogData.otype == 1) return this.dialogData.newprice + this.float
                    else return this.dialogData.newprice - this.float
                } else {
                    return this.dialogData.newprice
                }
            },
            //  点差*最小变动价  
            float() {
                return this.$public.Multiplication(this.spread, this.var_price)
            },
            // 手续费
            // fee() {
            //     var rate = Number(this.trans_fee.replace("%", "")) / 100
            //     return this.$public.toIntercept(this.$public.Multiplication(this.dialogData.newprice * this.dialogData.buynum * this.pcs_price, rate), 4)
            // },
            // 保留位数
            savePoint() {
                return this.$public.savePoint(this.markData.code)
            },
            // 张单价
            pcsPrice() {
                if (this.pcs_price) {
                    return parseFloat(Number(this.pcs_price))
                } else {
                    return '--'
                }
            },
            // 占用保证金
            // 交易总金额（保证金）=合约面值*持仓合约数量*持仓均价/杠杆
            buyTotal() {
                if (!this.pcs_price) return '--'
                var val = this.$public.Division(
                    this.$public.Multiplication(this.pcs_price * this.buy.buynum, this.buy.newprice)
                    , this.buy.leverage
                )
                return this.$public.toIntercept(val, 4, true)
            },
            sellTotal() {
                if (!this.pcs_price) return '--'
                var val = this.$public.Division(
                    this.$public.Multiplication(this.pcs_price * this.sell.buynum, this.sell.newprice)
                    , this.sell.leverage
                )
                return this.$public.toIntercept(val, 4, true)
            },
            // 最大购买量
            maxBuyNum() {
                // 最大购买量 = (客户总钱数*出资比例)*杠杆  /  价格* 张单价
                var usabel = this.buyOutRate ? this.$public.Multiplication(this.buyOutRate, this.usabel) : this.usabel
                var price = this.buy.newprice
                if (this.isMarket) {
                    price = price + this.float
                }
                var max = this.$public.Division(
                    usabel * this.buy.leverage,
                    this.$public.Multiplication(
                        price, this.pcs_price
                    )
                )
                return ~~max
            },
            // 最大卖出量
            maxSellNum() {
                // 最大卖出量 = (客户总钱数*出资比例)*杠杆  /  价格* 张单价
                var usabel = this.sellOutRate ? this.$public.Multiplication(this.sellOutRate, this.usabel) : this.usabel
                var price = this.sell.newprice
                if (this.isMarket) {
                    price = price - this.float
                }
                var max = this.$public.Division(
                    usabel * this.sell.leverage,
                    this.$public.Multiplication(
                        price, this.pcs_price
                    )
                )
                return ~~max
            }
        },
        methods: {
            tips(val,value) {
                this.$public.msg(val,'error',this)
                this.$public.msg(value,'error',this)
            },
            // 填充价格
            fillPrice(price, type) {
                if (!this.isMarket) {
                    type === 'bid' ? this.sell.newprice = price : this.buy.newprice = price
                }
            },
            // 点击加减号
            countNum(model, type) {
                var min = () => {
                    var str = ['0.', '', '1']
                    for (var i = 1; i < this.savePoint; i++) {
                        str[1] = str[1] + '0'
                    }
                    return Number(str.join(""))
                };
                this[model].newprice =
                    type === '+' ?
                        this.$public.Addition(this[model].newprice, min()) :
                        this.$public.Subtraction(this[model].newprice, min())
            },
            clearForm() {
                this.buyOutRate = ""
                this.sellOutRate = ""
                this.buy.buynum = ""
                this.sell.buynum = ""
            },
            // 关闭下单弹框
            dialog_close() {
                this.dialog = false
            },
            // 下单
            createOrder(form) {
                // 登录判断
                if (!sessionStorage.token) {
                    this.$public.msg(this.$t('contract.tips[0]'), 'warning', this)
                    return
                }
                if (form.buynum == "") {
                    // 请输入数量
                    this.$public.msg(this.$t('contract.tips[1]'), 'warning', this)
                    return
                }
                this.dialogData = JSON.parse(JSON.stringify(form))
                this.dialogData.shopname = this.markData.code
                // 占用保证金
                form.otype == 1 ? this.dialogData.total = this.buyTotal : this.dialogData.total = this.sellTotal
                this.dialog = true
            },
            // 提交订单
            submitOrder() {
                var sendData = this.dialogData
                this.loading = true
                create_order(sendData).then((r) => {
                    this.loading = false
                    if (r.data.status == 200) {
                        // 下单成功！
                        this.$public.msg(this.$t('contract.tips[2]'), 'success', this)
                        this.$bus.$emit("order_success")
                        this.dialog = false
                        this.clearForm()
                    } else {
                        this.$public.msg(r.data.msg, 'warning', this)
                    }
                }).catch((r) => { this.loading = false })
            },
            // 更改市价限价
            changeType(bl) {
                this.isMarket = bl
            },
            // 获取币种设置信息
            getMarkInfo(data) {
                this.markData = data
                this.buy.newprice = data.price
                this.sell.newprice = data.price
                this.pcs_price = ""
                get_lever({ code: data.code }).then((r) => {
                    if (r.data.status) {
                        var info = r.data.data
                        this.leverageArr = info.leverage.split(",")
                        this.pcs_price = info.pcs_price
                        this.var_price = info.var_price
                        this.trans_fee = info.trans_fee
                        this.spread = info.spread
                        // 默认选择第一个
                        this.buy.leverage = this.leverageArr[0]
                        this.sell.leverage = this.leverageArr[0]
                    }
                }).catch((r) => {
                    console.log(r);
                })
            },
            // 更新输入框价格
            updatePrice(data) {
                if (this.isMarket) {
                    this.buy.newprice = data.price
                    this.sell.newprice = data.price
                }
            },
            // 改变出资比例
            changeOutRate(rate, type) {
                if (this[type] == rate) {
                    this[type] = ""
                } else {
                    this[type] = rate
                }
            },
            updateBalance(data) {
                this.usabel = data.balance
            }
        },
        created() {
            // 获取余额
            this.$bus.$on("getBalance", this.updateBalance)
            // 进入页面第一次获取币种标识
            this.$bus.$on('Mark_init', this.getMarkInfo)
            // 点击币种被改变
            this.$bus.$on('Mark_change', this.getMarkInfo)
            // 推送更新当前币种数据
            this.$bus.$on('markData_updated', this.updatePrice)
            // 盘口点击价格 填充输入价
            this.$bus.$on('bidask_to_trader', this.fillPrice)
        },
        beforeDestroy() {
            this.$bus.$off("getBalance", this.updateBalance)
            this.$bus.$off("Mark_init", this.getMarkInfo)
            this.$bus.$off("Mark_change", this.getMarkInfo)
            this.$bus.$off("markData_updated", this.updatePrice)
            this.$bus.$off("bidask_to_trader", this.fillPrice)
        }
    }

</script>
<style lang=less>
    @import '../../../assets/index.less';
    @import './trader.less';
</style>


