<template>
  <div class="tradingAccount-wrapper">
      <!-- 交易账户 -->
    <h4>{{$t('contract.account[0]')}}</h4>
    <div class="usable" v-if="isOn">{{usabel}}</div>
    <div class="usable" v-else>*****</div>
    <div class="currency">USDT</div>
    <img @click="change" :src="isOn ? iconBase64.openEye : iconBase64.closeEye " alt="eye">
  </div>
</template>

<script>
  import { getBalance } from "@/api/contract"
  export default {
    data() {
      return {
        usabel: '--',
        isOn: true,
        iconBase64: {
          openEye: 'data:image/png;base64,',
          closeEye: 'data:image/png;base64,'
        }
      };
    },
    methods: {
      change() {
        this.isOn = !this.isOn
        sessionStorage.setItem('c_isOn', JSON.stringify(this.isOn))
      },
      updateBalance(data) {
        this.usabel = data.balance
      }
    },
    computed: {},
    created() {
      var _this = this
      this.isOn = JSON.parse(sessionStorage.getItem('c_isOn')) === null ? true : JSON.parse(sessionStorage.getItem('c_isOn'))
      if (sessionStorage.token && sessionStorage.account) {
        this.$bus.$on('getBalance', this.updateBalance)
      }
    },
    beforeDestroy() {
      this.$bus.$off('getBalance',  this.updateBalance)
    }
  }
</script>
<style lang=less scoped>
  /* 背景图片 */
  .tradingAccount-wrapper {
    width: 100%;
    height: 92px;
    position: relative;
    padding: 12px 20px;
    color: #fff;
    background: url("../../../assets/img/exchange/acbg.png") no-repeat;
    background-size: 100% 100%;
    box-sizing: border-box;
    font-size: 14px;
    margin-bottom: 5px;

    img {
      position: absolute;
      top: 15px;
      right: 6px;
      cursor: pointer;

    }

    h4 {
      font-size: 16px;
    }

    .usable {
      font-size: 20px;
      margin-top: 15px;
      margin-bottom: 8px;
      font-weight: bold;
    }
  }
</style>


