<!-- 合约页面内 交易记录 -->
<template>
  <div class="contractRecord-part-wrapper">
    <div class="contractRecord-head">
      <div class="radio-group">
        <div class="item" @click="activeName = '持仓'" :class="activeName == '持仓' ? 'active' : '' ">{{$t('contract.record[0]')}}</div>
        <div class="item" @click="activeName = '委托'" :class="activeName == '委托' ? 'active' : '' ">{{$t('contract.record[1]')}}</div>
        <div class="item" @click="activeName = '历史'" :class="activeName == '历史' ? 'active' : '' ">{{$t('contract.record[2]')}}</div>
      </div>
      <div class="warning">
          <!-- 浮动盈亏： 爆仓率： -->
        <div class="yk">{{$t('contract.record[3]')}}：<span>{{ $public.toIntercept(data.yingkui, 4,true) }}</span></div>
        <div class="fx">{{$t('contract.record[4]')}}：<span>{{data.risk}}</span></div>
      </div>
    </div>
    <div class="contractRecord-main">
      <EnTrustTabel :loop='looptime' v-if="activeName == '委托'"></EnTrustTabel>
      <PositionTabel :loop='looptime' v-if="activeName == '持仓'"></PositionTabel>
      <HistroyTabel :loop='looptime' v-if="activeName == '历史'"></HistroyTabel>
    </div>
  </div>
</template>

<script>
  import { tongji } from '@/api/contract'
  import EnTrustTabel from '@/components/contractRecord/enTrustTabel.vue'
  import PositionTabel from '@/components/contractRecord/positionTabel.vue'
  import HistroyTabel from '@/components/contractRecord/histroyTabel.vue'
  export default {
    data() {
      return {
        activeName: '持仓',
        data: "",
        timer: "",
        // 轮询时间
        looptime:3000
      };
    },

    components: {
      EnTrustTabel,
      PositionTabel,
      HistroyTabel
    },

    created() {
      if(sessionStorage.token){
        this.getTongji()
      }
    },

    methods: {
      changeText(val) {
        this.text = val
      },
      getTongji() {
        this.timer = setInterval(() => {
          tongji({ account: JSON.parse(sessionStorage.account) }).then((r) => {
            if(r.data.status ==200){
              this.data = r.data.data
            }

          })
        }, this.looptime)

      }
    },
    beforeDestroy() {
      window.clearInterval(this.timer)
    }

  }

</script>
<style lang=less>
  @import '../../../assets/index.less';
  @import './record.less';
</style>


