<!-- 合约 -->

<template>
    <div class="contract-wrapper">
        <!-- 左侧区域 -->
        <div class="contract-left float">
            <!-- 交易账户 -->
            <Trading-account></Trading-account>
            <!-- 行情列表 -->
            <Quotation-list></Quotation-list>
        </div>

        <!-- 中间部分 -->
        <div class="contract-middle float">
            <!-- 图表 -->
            <Chart></Chart>
            <!-- 记录 -->
            <Record></Record>
        </div>

        <!-- 右侧区域 -->
        <div class="contract-right float">
            <div class="contract-rigth_mark">
                <!-- 盘口 -->
                <Bid-Ask></Bid-Ask>
                <!-- 实时成交 -->
                <Real-time></Real-time>
            </div>
            <!-- 交易组件 -->
            <div class="contract-right_trade">
                <Trader></Trader>
            </div>
        </div>
    </div>
</template>

<script>
    /*
    使用$on $emit 解决组件通信耦合度高的问题
    注意：
    1.组件销毁前使用$off取消监听,否则可能会触发多次
    2.非特殊情况将$on放在created中,$on必须在$emit前执行
    */
 
    // 交易账户
    import TradingAccount from './tradingAccount/tradingAccount.vue'
    // 行情列表
    import QuotationList from './quotation/quotationList.vue'
    // 实时成交
    import RealTime from './realTime/realTime.vue'
    // 盘口
    import BidAsk from './bid-ask/bid-ask.vue'
    // 交易组件
    import Trader from './trader/trader.vue'
    // 图表部分
    import Chart from './chart/chart.vue'
    // 记录
    import Record from './record/record.vue'


    import {
        getPro, indexGetpro, getdepth,
        getBalance, realTime, mainPushUrl,
        realPushUrl, bidaskPushUrl
    } from '@/api/contract'


    export default {
        data() {
            return {
                //重要 当前所选币种标识 被改变后更新多处内容 两种情况被改变：
                // 1. 首次进入页面请求币种列表，Mark从空变为列表第一个，改变后触发$emit('Mark_init')
                // 2. 点击行情列表的币种，Mark变为所选标识 触发$emit('Mark_change')
                // 以上两个事件均携带markData
                Mark: '',
                // 当前币种标识对应的数据 
                markData: "",
                // 推送实例
                mainPusher: "",
                realTimePusher: "",
                bidaskPusher: "",
            };
        },
        components: {
            TradingAccount,
            QuotationList,
            RealTime,
            BidAsk,
            Trader,
            Chart,
            Record
        },
        computed: {},

        watch: {
            'Mark'(n, o) {
                this.bidaskInit()
                if (o == "") return
                // 第一次进入页面时不会触发
                this.sendMsg()
            }
        },

        methods: {
            // 行情列表 点击币种会触发
            changeMark(data) {
                this.Mark = data.code
                this.markData = data
            },
            // 获取行情列表  只在进入页面时请求
            getQuotationList() {
                indexGetpro().then((r) => {
                    if (r.data.status == 200) {
                        this.Mark = r.data.data[0].code
                        this.markData = r.data.data[0]
                        var bus = { data: r.data.data }
                        this.$bus.$emit('quotationList_init', bus) // QuotationList 行情列表组件中监听
                        this.$bus.$emit('Mark_init', this.markData) // Mark首次获取时触发  
                        // 开启推送
                        this.mainPush()
                        this.realTimePush()
                        this.bidaskPush()
                    } else {
                        this.$public.msg(r.data.msg, 'error', this)
                    }

                }).catch((r) => { console.log(r); })
            },
            // 行情推送
            mainPush() {
                if ("WebSocket" in window) {
                    this.mainPusher = new WebSocket(mainPushUrl);
                } else if ("MozWebSocket" in window) {
                    this.mainPusher = new WebSocket(mainPushUrl);
                } else {
                    this.mainPusher = new WebSocket(mainPushUrl);
                }
                this.mainPusher.onopen = () => {
                    var msg = { code: 'all' }
                    this.mainPusher.send(JSON.stringify(msg))
                }
                this.mainPusher.onmessage = (msg) => {
                    if (this.$route.path !== '/contract') {
                        this.mainPusher.close()
                        return
                    }
                    if(msg.data.split("{").length == 2) return
                    var data = JSON.parse(msg.data)
                    // 筛选当前币种数据
                    if (data.code === this.Mark) {
                        this.markData = data
                        // 更新当币种数据
                        this.$bus.$emit("markData_updated", this.markData)
                    }
                    // 通知行情列表组件更新
                    this.$bus.$emit('quotationList_updated', data)
                }
                this.mainPusher.onerror = (msg) => {
                    console.log("  websocket.onerror in mainPusher");
                };
            },
            // 盘口首次ajax获取数据
            bidaskInit() {
                var sendData = { code: this.Mark }
                getdepth(sendData).then((r) => {
                    if (r.data.status == 200) {
                        var bus = { data: r.data.data, Mark: this.Mark }
                        this.$bus.$emit('bidask_init', bus) // bidask 盘口组件中监听
                    } else {
                        this.$public.msg(r.data.msg, 'error', this)
                    }
                }).catch((r) => { console.log(r) })
            },
            // 盘口推送
            bidaskPush() {
                if ("WebSocket" in window) {
                    this.bidaskPusher = new WebSocket(bidaskPushUrl);
                } else if ("MozWebSocket" in window) {
                    this.bidaskPusher = new WebSocket(bidaskPushUrl);
                } else {
                    this.bidaskPusher = new WebSocket(bidaskPushUrl);
                }
                this.bidaskPusher.onopen = () => {
                    this.bidaskPusher.send(JSON.stringify({ code: this.Mark }))
                }
                this.bidaskPusher.onmessage = (msg) => {
                    if (this.$route.path !== '/contract') {
                        this.bidaskPusher.close()
                        return
                    }
                    var data = JSON.parse(msg.data)
                    var bus = { data }
                    // 通知盘口组件更新
                    this.$bus.$emit('bidask_update', bus)
                }
                this.bidaskPusher.onerror = (msg) => {
                    console.log("  websocket.onerror in bidaskPusher");
                };

            },
            // 实时成交首次ajax获取数据 该项目没有实时成交ajax
            realTimeInit() {

            },
            // 实时成交推送
            realTimePush() {
                if ("WebSocket" in window) {
                    this.realTimePusher = new WebSocket(realPushUrl);
                } else if ("MozWebSocket" in window) {
                    this.realTimePusher = new WebSocket(realPushUrl);
                } else {
                    this.realTimePusher = new WebSocket(realPushUrl);
                }
                this.realTimePusher.onopen = () => {
                    this.realTimePusher.send(JSON.stringify({ code: this.Mark }))
                }
                this.realTimePusher.onmessage = (msg) => {
                    if (this.$route.path !== '/contract') {
                        this.realTimePusher.close()
                        return
                    }
                    var data = JSON.parse(msg.data)
                    var bus = { data }
                    // 通知盘口组件更新
                    this.$bus.$emit('realTime_updated', bus)
                }
                this.realTimePusher.onerror = (msg) => {
                    console.log("  websocket.onerror in realTimePusher");
                };
            },
            // 改变推送
            sendMsg() {
                this.realTimePusher.send(
                    JSON.stringify({ code: this.Mark })
                );
                this.bidaskPusher.send(
                    JSON.stringify({ code: this.Mark })
                );
            },
            // 获取余额
            getBalance() {
                if (!sessionStorage.token) return
                getBalance().then((r) => {
                    if (r.data.status == 200) {
                        this.$bus.$emit('getBalance', r.data.data)
                    } else {
                        this.$public.msg(r.data.msg, 'error', this)
                    }
                })
            },

        },
        created() {
            // 监听当前币种标识改变  行情列表组件中触发
            this.$bus.$on('Mark_change', this.changeMark)
            // 下单成功 更新余额 
            this.$bus.$on('order_success', this.getBalance)
            // 获取行情列表
            this.getQuotationList()
            // 获取余额
            this.getBalance()
        },
        beforeDestroy() {
            // 取消监听
            this.$bus.$off('Mark_change', this.changeMark)
            this.$bus.$off('order_success', this.getBalance)

        }
    }

</script>
<style lang=less>
    @import '../../assets/index.less';
    @import './contract.less';
</style>


