<template>
  <div class="chart-wrapper">
    <!-- k线头部 -->
    <div class="chart-head">
      <div class="name">{{name}}</div>
      <div class="price">{{$public.transPrice(markData.price, markData.code)}} <span
          class='cnyPrice'>≈{{$public.toIntercept(markData.cnyPrice, 2)}} CNY</span></div>
          <!-- 涨幅 高 低 24量-->
      <div class="changeRate">{{$t('contract.chart[0]')}}<span :class="markData.changeRate[0]=='-'?'red':'green'">{{markData.changeRate}}</span>
      </div>
      <div class="high ml20">{{$t('contract.chart[1]')}}<span class="">{{markData.high}}</span></div>
      <div class="high ml20">{{$t('contract.chart[2]')}}<span class="">{{markData.low}}</span></div>
      <div class="high ml20">{{$t('contract.chart[3]')}}<span class="">{{$public.toIntercept(markData.volume, 2)}} USDT</span></div>
      <div class="switch-operate ">
          <!-- K线图 深度图 -->
        <div class="btn" :class="type=='kline'?'active':''" @click="type = 'kline'">{{$t('contract.chart[4]')}}</div>
        <div class="btn" :class="type=='deep'?'active':''" @click="type = 'deep'">{{$t('contract.chart[5]')}}</div>
      </div>

    </div>
    <div class="chart-main">
        <iframe name="iframeChart" id="iframeChart1" ref="f1" v-if="type == 'kline' && curData.code" :src="`./static/index.html?timers=5&code=${curData.code}&last_price=${curData.price}&theme=${theme}`" width="100%" height="100%"
        frameborder="0"></iframe>
      <iframe name="iframeChart" id="iframeChart2" ref="f2" v-if="type == 'deep'" :src="`./static/indexsd.html?timers=5&code=${curData.code}&last_price=${curData.price}&theme=${theme}`" width="100%" height="100%"
        frameborder="0"></iframe>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        type:"kline",
        // 触发iframe重载
        theme:"",
        curData:"",
        markData: {
          name: "--",
          changeRate: "",
        }
      };
    },
    components: {},
    computed: {
      name() {
        var arr = this.markData.name.split("_")
        if (arr.length == 2) {
          arr[1] = '/USDT'
          return arr.join("")
        } else {
          return arr[0] + '/USDT'
        }
      }
    },
    methods: {
      init(data) {
        this.curData = data
        this.markData = data

      },
      updateData(data) {
        this.markData = data;
      },
      updateTime(val) {
        this.theme =val
      }
    },
    created() {
      // 进入页面初始化当前币种数据
      this.$bus.$on('Mark_init', this.init)
      // 点击币种 
      this.$bus.$on('Mark_change', this.init)
      // 推送更新当前币种数据
      this.$bus.$on('markData_updated', this.updateData)
      this.$bus.$on('changeTheme', this.updateTime)
    },
    beforeDestroy() {
      this.$bus.$off('Mark_init', this.init)
      this.$bus.$off('Mark_change',  this.init)
      this.$bus.$off('markData_updated', this.updateData)
      this.$bus.$off('changeTheme', this.updateTime)
    }
  }

</script>
<style lang=less>
  @import "../../../assets/index.less";
  @import "./chart.less";
</style>


