<template>
    <div class="bid-ask-wrapper">
        <div class="bid-ask-head">
            <div class="bid-ask-head-top">
                <div class="slt-group">
                    <img @click="showModel = 'all'" :src="imgBase64.all" alt="">
                    <img @click="showModel = 'bid'" :src="imgBase64.bid" alt="">
                    <img @click="showModel = 'ask'" :src="imgBase64.ask" alt="">
                </div>
                <div class="deepMerge" v-show="deepShow">
                    <!-- 深度合并 -->
                    {{$t('contract.bid[0]')}}
                    <el-select v-model="deep" :placeholder="$t('contract.bid[1]')">
                        <el-option v-for="i in deepMerge" :label='i' :value='i' :key="i">
                        </el-option>
                    </el-select>
                </div>

            </div>
            <div class="bid-ask-head-bottom">
            <!-- 价格 数量 累计 -->
                <div class="price cell">{{$t('contract.bid[2]')}}</div>
                <div class="number cell">{{$t('contract.bid[3]')}}</div>
                <div class="amount cell">{{$t('contract.bid[4]')}}</div>
            </div>
        </div>
        <div v-if="data">
            <!-- 卖 -->
            <ul class="bid-ask-list ask" id="ul_ask" @click="fillPrice($event, 'ask')" v-show="showModel == 'ask' || showModel == 'all'"
                :style="`height:${listHeight}`">
                <li v-for="(item,index) in data.asks" :key="index" >
                    <span class='price cell red'>{{transDeep( $public.transPrice(item.price, Mark) )}}</span>
                    <span class='number cell'>{{$public.toIntercept(item.totalSize, 4, true) }}</span>
                    <span
                        class='amount cell'>{{$public.toIntercept($public.Multiplication(item.price, item.totalSize, true), 4)}}</span>
                    <div class="ask_bg" :style="`width:${item.totalSize/askMax * 100}%`"></div>
                </li>
            </ul>
            <!-- 最新价 -->
            <div class="newPrice" :class="newPrice.rate =='↑'? 'green':'red '" id="bidask_newPrice"
                v-show="showModel=='all'">
                <span class="price">{{newPrice.price}}</span> <span class="rate">{{newPrice.rate}}</span> <span
                    class="newPrice-cny">{{$public.toIntercept(newPrice.cny, 2)}} CNY</span>
            </div>
            <!-- 买 -->
            <ul class="bid-ask-list bid" @click="fillPrice($event,'bid')" id="ul_bid" v-show="showModel == 'bid' || showModel == 'all'"
                :style="`height:${listHeight}`">
                <li v-for="(item,index) in data.bids">
                    <span class='price cell green'>{{transDeep($public.transPrice(item.price, Mark)) }}</span>
                    <span class='number cell'>{{$public.toIntercept(item.totalSize, 4) }}</span>
                    <span class='amount cell'>{{$public.toIntercept($public.Multiplication(item.price, item.totalSize), 4)}}</span>
                    <div class="bid_bg" :style="`width:${item.totalSize/bidMax * 100}%`"></div>
                </li>
            </ul>
        </div>


    </div>
</template>

<script>
    export default {
        data() {
            return {
                Mark: '',
                markData: "",
                deep: '',
                showModel: 'all',
                data: "",
                askMax: "",
                bidMax: "",
                imgBase64: {
                    all: 'data:image/png;base64,',
                    bid: 'data:image/png;base64,',
                    ask: 'data:image/png;base64,',
                },

            };
        },
        components: {},
        computed: {
            deepMerge() {
                return this.$public.savePoint(this.Mark)
            },
            deepShow() {
                if(this.markData.price < 1 ) {
                    return false
                }else{
                    return true
                }
            },
            newPrice() {
                if (this.markData === "") {
                    return { price: '--', rate: '', cny: '--' }
                } else {
                    return {
                        price: this.$public.transPrice(this.markData.price, this.markData.code),
                        rate: this.markData.changeRate[0] === '-' ? '↓' : '↑',
                        cny: this.markData.cnyPrice
                    }
                }
            },
            listHeight() {
                // 通过改变高度来实现 切换买卖盘口 父元素 overflow：hidden
                if (this.showModel == 'all') {
                    return '250px'
                } else {
                    return '540px'
                }

            }
        },
        created() {
            // 监听第一次ajax请求
            this.$bus.$on('bidask_init', this.init)
            // 监听推送更新
            this.$bus.$on('bidask_update', this.update)
            // 进入页面初始化当前币种数据
            this.$bus.$on('Mark_init', this.markDataFunc)
            // 点击币种 
            this.$bus.$on('Mark_change', this.markDataFunc)
            // 推送更新当前币种数据
            this.$bus.$on('markData_updated', this.markDataFunc)

        },
        methods: {
            markDataFunc(data) {
                this.markData = data
            },
            // 盘口价格填充到交易组件买卖价input
            fillPrice(e,type){
                var price = e.path[1].children[0].innerText
                this.$bus.$emit('bidask_to_trader', price,type)
            },
            // 盘口ajax初始化
            init(bus) {
                this.Mark = bus.Mark
                this.data = bus.data[0]
                this.deep = ""
            },
            // 推送更新
            update(bus) {
                var _this = this
                var bidDom = document.getElementById('ul_bid')
                var askDom = document.getElementById('ul_ask')
                // 获取当前数据中购买的最大量 渲染背景条用
                var bidArr = bus.data.bids.concat().sort((a, b) => {
                    return b.totalSize - a.totalSize;
                });
                var askArr = bus.data.asks.concat().sort((a, b) => {
                    return b.totalSize - a.totalSize;
                });
                this.askMax = askArr[0].totalSize
                this.bidMax = bidArr[0].totalSize
                bus.data.asks.reverse()
                // 渲染dom 
                bidDom.innerHTML = this.createHtml(bus.data.bids, 'green' ,this.bidMax)
                askDom.innerHTML = this.createHtml(bus.data.asks, 'red' , this.askMax)
            },
            createHtml(data, color, max) {
                var html = ''
                for (var i = 0; i < data.length; i++) {
                    html += `<li v-for="(item,index) in data.bids">
                    <span class='price cell ${color} '>${this.transDeep(this.$public.transPrice(data[i].price, this.Mark))}</span>
                    <span class='number cell'>${this.$public.toIntercept(data[i].totalSize, 4, true)}</span>
                    <span class='amount cell'>${this.$public.toIntercept(this.$public.Multiplication(data[i].price, data[i].totalSize), 4, true)}</span>
                    <div class="${color=='green'?'bid_bg':'ask_bg'}" style="width:${data[i].totalSize/max * 100}%"></div>
                    </li>`
                }
                return html
            },
            // 深度合并 
            transDeep(val) {
                var int = val.split(".")[0] //整数部分
                var de = val.split(".")[1] //小数部分
                var arr = de.split("") // 小数转数组
                var str; // 处理后的小数部分字符串
                if (this.deep === "") return val
                for (var i = 0; i < de.length; i++) {
                    if (i + 1 > Number(this.deep)) {
                        arr.splice(i, 1, 0)
                    }
                }
                str = arr.join("")
                return int + '.' + str
            }
        },
        beforeDestroy() {
            this.$off('bidask_init', this.init)
            this.$off('bidask_update',  this.update)
            this.$off('Mark_change', this.markDataFunc)
            this.$off('Mark_init', this.markDataFunc)
            this.$off('markData_updated', this.markDataFunc)
        }
    }

</script>
<style lang=less>
    @import '../../../assets/index.less';

    @import './bid-ask.less';
</style>


