<template>
    <div class="quotationList-wrapper">
        <h2>
                <!-- 资产行情 -->
            {{$t('contract.quotationList[0]')}}
        </h2>
        <ul class="quotationList">
            <li @click="clickMark(item)" v-for="(item,index) in data" :key='index'>
                <img :src="$public.transCodeImg(item.name)" alt="图片丢了"/>
                <div class="info">
                    <div class="info-top">
                        <div class="currency">
                            {{item.name}}
                        </div>
                        <div class="price" :id="`${item.code}_price`">
                            {{$public.transPrice(item.price,item.code) }}
                        </div>
                    </div>
                    <div class="info-bottom">
                        <div class="cnyPrice" :id="`${item.code}_cnyPrice`">
                            ≈{{$public.toIntercept(item.cnyPrice, 2)  }}
                        </div>
                        <div class="changeRate" :id="`${item.code}_changeRate`">
                            <span :class="item.changeRate[0] == '-'? 'red' : 'green'">
                                {{item.changeRate}}
                            </span>
                        </div>
                    </div>
                </div>
            </li>
        </ul>
    </div>
</template>
<script>
  export default {
    data() {
      return {
          data:"",
          markData:"",
      };
    },

    methods: {
        // ajax初始化列表
        init(bus) {
            this.data = bus.data
        },
        //  推送更新dom
        update(data) {
            // 动态获取dom
            var priceDom = document.getElementById(`${data.code}_price`)
            var cnyPriceDom = document.getElementById(`${data.code}_cnyPrice`)
            var changeRateDom = document.getElementById(`${data.code}_changeRate`)
            // 渲染
            priceDom.innerHTML = this.$public.transPrice(data.price,data.code) 
            cnyPriceDom.innerHTML ='≈' + this.$public.toIntercept(data.cnyPrice, 2)  
            changeRateDom.innerHTML = `<span class="${data.changeRate[0] == '-'? 'red' : 'green'}">${data.changeRate}</span>`
        },
        // 改变当前币种标识
        clickMark(data){
            this.$bus.$emit('Mark_change', data) 
        }
    },
    created() {
        this.$bus.$on('quotationList_init', this.init)
        this.$bus.$on('quotationList_updated', this.update)
    },
    beforeDestroy() {
        this.$bus.$off('quotationList_init', this.init)
        this.$bus.$off('quotationList_updated', this.update)
    }

  }

</script>
<style lang=less>
  @import '../../../assets/index.less';
  @import './quotationList.less';


</style>



